/* MuiOutlinedInput-root MuiInputBase-formControl */
table {
  border-spacing: 0;
  border: 1px solid #ededed;
  /* margin-top: 65px; */
  max-width: 100%;
  /* max-height: 200px; */
  position: absolute;
}

table body {
  max-height: 300px;
}

table tr:last-child td {
  border-bottom: 0;
}

table th {
  /* position: fixed; */
  overflow-y: scroll;
}

table th, table td {
  /* margin: 0; */
  padding: 0.5rem;
  border-bottom: 1px solid #ededed;
  border-right: 1px solid #ededed;
  position: relative;
}

table th:last-child,
table td:last-child {
  border-right: 0;
}
table tr:nth-child(even) {
  background-color: #fafafa;
}


/* search table data */
.searchField{
  min-width: 500px;
  min-height: 40px;
  display: flex;
  border: 1px solid #256ead;
  border-radius: 5px;
  /* margin-top: 25px;
  margin-bottom: 15px; */
  font-style: italic;
  min-width: 30%;
}

::placeholder { /* Chrome, Firefox, Opera, Safari 10.1+ */
  color: #256ead;
  opacity: 1; /* Firefox */
  padding-left: 10px;
}

